@import '../../helpers/helpers.scss';

.projectModal {
  width: 80%;
  max-width: 550px;
  background-color: #fff;
  padding: 15px;
  border-radius: 10px;
  position: relative;

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__wrapper {
    display: flex;
    gap: 10px;
    align-items: center;

    margin-bottom: 5px;

    @include tablet() {
      margin-bottom: 10px;
      gap: 15px;
    }

    @include desktop() {
      margin-bottom: 15px;
      gap: 20px;
    }
  }

  &__title {
    font-size: 16px;

    @include tablet() {
      font-size: 18px
    }

    @include desktop() {
      font-size: 20px;
    }

    &-sub {
      font-size: 12px;

      @include tablet() {
        font-size: 14px
      }
  
      @include desktop() {
        font-size: 16px;
      }
    }
  }

  &__arrows {
    display: flex;
    flex-direction: column;
    gap: 2px;
    &-up {
      transform: rotate(90deg);

      background-image: url('../../svg/arrow-left--dark.png');
      width: 5px;
      height: 5px;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &-down {
      transform: rotate(90deg);

      background-image: url('../../svg/arrow-right--dark.png');
      width: 5px;
      height: 5px;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  &__actions {
    display: flex;
    flex-direction: row;
    position: relative;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 2px;
    margin-top: 10px;
  }

  &__container {
    margin-top: 10px;

    >h2 {
      font-size: 20px;
    }
  }

  &__member {
    display: flex;
    align-items: center;
    gap: 5px;

    &-position {
      font-size: 8px;
      font-weight: 500;
      color: $color-grey--dark;
  
      @include tablet() {
        font-size: 10px;
      }
  
      @include desktop() {
        font-size: 12px;
      }
    }
  }

  &__remove {
    position: absolute;
    right: 0;
    bottom: -20px;
  
    @include tablet() {
      bottom: -25px;
    }
  
    @include desktop() {
      bottom: -30px;
    }
  }

  &__specialization {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    width: 100%;

    max-width: 200px;

    @include tablet() {
      max-width: 240px;
    }

    @include desktop() {
      max-width: 300px;
    }

    &-title {
      font-size: 10px;
      font-weight: 300;

      @include tablet() {
        font-size: 12px;
      }
  
      @include desktop() {
        font-size: 14px;
      }
    }
  }

  &__button {
    display: flex;
    align-items: center;
    gap: 5px;
  }

  &__bottom {
    flex-direction: row;
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 20px;

    >div {
      display: flex;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    gap: 5px;

    &-apply {
      display: flex;
      align-items: center;
      gap: 10px;
    }
  }
}
