@import '../../../helpers/helpers.scss';

.request {
  display: flex;
  gap: 5px;
  position: relative;

  &__wrapper {
    width: 100%;
    padding: 3px;
    border-radius: 5px;
    box-shadow: 0px 0px 1px 1px rgba($color: #000, $alpha: .1);
    position: relative;
  }

  &__shell {
    width: 100%;
  }

  &__container {
    display: flex;
    flex-direction: row;
    gap: 5px;
  }

  &__user {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 6px;

    @include tablet() {
      font-size: 8px;
    }

    @include desktop() {
      font-size: 10px;
    }
  }

  &__position {
    color: $color-purple;
    font-weight: 600;
  }

  &__message {
    width: 100%;
    height: 55px;
    padding: 3px;
    font-size: 6px;
    background-color: $color-grey--lite;
    border-radius: 5px;
    overflow: scroll;

    @include tablet() {
      padding: 4px;
      font-size: 8px;
      height: 75px;
    }

    @include desktop() {
      padding: 5px;
      font-size: 10px;
      height: 85px;
    }
  }

  &__buttons {
    display: flex;
    gap: 5px;
    flex-direction: column;
    justify-content: flex-end;
  }

  &__status {
    position: absolute;
    right: 2px;
    top: 2px;
  }

  &__field {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 50px;
    min-height: 55px;

    @include tablet() {
      min-width: 60px;
      min-height: 75px;
    }

    @include desktop() {
      min-width: 80px;
      min-height: 85px;
    }
  }

  &__selected {
    position: absolute;
    width: 10px;
    top: 0;
    bottom: 0;
    left: -13px;
    background-color: $color-purple;
    border-radius: 5px;

    display: flex;
    align-items: center;
    justify-content: center;

    &_icon {
      transform: scale(.3);
    }
  }

  &__deleted {
    min-width: 88px;
    min-height: 74.5px;

    display: flex;
    align-items: center;
    justify-content: center;

    &_icon {
      transform: scale(2.5);
    }

    @include tablet() {
      min-width: 91px;
      min-height: 95px;
    }
  }
}
