@import '../../helpers/helpers.scss';

.empty {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  &__img {
    background-image: url('../../svg/icon-empty.png');
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-size: contain;

    @include tablet() {
      width: 50px;
      height: 50px;
    } 
   
    @include desktop() {
      width: 60px;
      height: 60px;
    }
  }
}
