@import '../../../helpers/helpers.scss';

.speciality {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 8px;
  color: $color-orange;

  @include tablet() {
    font-size: 10px;
  }

  @include desktop() {
    font-size: 12px;
  }

  &-icon {
    position: relative;
    top: 1.5px;
    transform: scale(.7);
  }

  &-include {
    width: 3px;
    height: 3px;
    background-color: $color-green;
    border-radius: 50%;

    @include tablet() {
      width: 5px;
      height: 5px;
    }
  }
}
